<template>
  <div class="big-table-wrap" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">手机号/用户号：</span>
            <Input v-model="phoneOruserName" placeholder="手机号/用户号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="10">
          <div  class="input-item">
            <daterange :name="'日期'" ref="regTime"  @start-time="timeChange('start',$event)"  @end-time="timeChange('end',$event)"></daterange>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="reSearchInfo" >查询</Button>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="resetSearch" >重置</Button>
          </div>
        </Col>
      </Row>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap" >
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border>
      </Table>
    </div>
  </div>
</template>
<script>
  import { tableMethods } from "./../../js/mixintable";
  import daterange from './../components/daterange'
  export default {
    data () {
      return {
        /*搜索区域 条件*/
        phoneOruserName:'',// 手机号，用户名
        startTime:'',//开始时间
        endTime:'',// 结束时间
        loading:false,
        /*table表格*/
        tableColumns: [
          {
            title: '用户名',
            key: 'user_no',
            align: 'center',
            width:150
          },
          {
            title: '实名',
            key: 'user_status',
            align: 'center',
          },
          {
            title: '最后登陆时间',
            key: 'op_time',
            align: 'center',
          },
          {
            title: '注册时间',
            key: 'register_time',
            align: 'center',
          },
          {
            title: '邀请人数',
            align: 'center',
            key: 'countInvite',
          },
          {
            title: '交易次数',
            align: 'center',
            key: 'countOrder',
          },
          {
            title: '绑定商户',
            align: 'center',
            key: 'namelist',
          }
        ]
      }
    },
    components: {
      daterange
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //查询数据
       // this.searchInfo();
      })
    },
    methods: {
      //重置查询条件
      resetSearch(){
        /*搜索区域 条件*/
        this.phoneOruserName=''//
        this.$refs.regTime.resetTime()
        this.startTime='' //开始时间
        this.endTime='' //结束时间
      },
      //查询数据
      searchInfo(){
        let objData={
          user_key:this.phoneOruserName.length==11?("+86 "+this.phoneOruserName):this.phoneOruserName,
          min_time:this.startTime.length>0?this.startTime.replace(/-/g,''):'',
          max_time:this.endTime.length>0?this.endTime.replace(/-/g,''):'',
        };
        let jsonData = protocol.getJsonParams(protocol.queryPeopleInfo, objData);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.$Loading.finish();
          /*当有数据的时候*/
          if(data.total-0>0){
            let resultData=''
            /*将绑定的商户循环展示出来*/
            if(data.returnList&&data.returnList.length>0){
              data.returnList.forEach(function (item,index) {
                resultData+=(item.point_name+',')
              })
              resultData=resultData.substr(0,resultData.length-1)
            }
            data.namelist=resultData
            this.tableData = [data];
          }else{//当没有数据的时候
            this.tableData = [];
          }
        })
      },
      //获取时间变化并赋值
      timeChange(e,time){
        if(e=="start"){
          this.startTime=time
        }else if(e=="end"){
          this.endTime=time
        }
      },
    },
  }
</script>
<style lang="less" scoped>
  @import './../../css/common.css';
</style>



